import { pages } from "projectConfig"
import React from "react"
import Img from "gatsby-image"
import { GatsbySeo } from "gatsby-plugin-next-seo"
import Layout from "globalLayoutsStaticPage"
import { useAllImagesHomeSlider } from "projectGraphqlStaticQueries/images-slider-home"

export default function Offer002Page(props) {
  const allImages = useAllImagesHomeSlider()

  return (
    <>
      <GatsbySeo
        title={pages.special.offer002.title}
        description={pages.special.offer002.description}
        canonical={props.location.href}
        openGraph={{
          url: props.location.href,
          title: pages.special.offer002.title,
          description: pages.special.offer002.description,
        }}
      />
      <Layout>
        <article className={"adaptive text"}>
          {allImages.map(item => {
            let imageFluid = item.node.childImageSharp.fluid
            let imageRelativePath = item.node.relativePath
            if (imageRelativePath === "assets/special-offers/carousel/002.jpg")
              return (
                <Img
                  fluid={imageFluid}
                  key={imageRelativePath}
                  className={"mb-4"}
                ></Img>
              )
          })}
          <h1 className={"mb-4"}>
            Успей выиграть премиум танк Löwe и 5.000 золота!
          </h1>
          <p>
            <b>Чтобы принять участие в разыгрыше нужно:</b>
          </p>
          <ol>
            <li>С 1-го по 28-е февраля купить любой товар в нашем магазине</li>
            <li>Подождать до 28-го февраля</li>
          </ol>
          <p>В конце месяца, на этой странице, будут объявлены победители:</p>
          <ul>
            <li>1-е место - премиум танк Löwe</li>
            <li>2-е место - 2.500 золота</li>
            <li>3-е место - 2.500 золота</li>
          </ul>
          <p>
            На почты победителей будут отправлены уведомления с просьбой выслать
            свой ник в WoT. После этого призы будут зачислены на аккаунты, как
            подарки.
          </p>
        </article>
      </Layout>
    </>
  )
}
